import React from 'react';
import "./productsPageGenerator.sass";
import Highlight from 'react-highlight';
import pagesContent from '../productsPageGenerator/pagesContent';

const ProductsPageGenerator = ({pageName}) => {
    const content = pagesContent[pageName].content;
    const code = pagesContent[pageName].code;

    return (
        <div className='homePage__wrapper'>
            <div className="pageGen_wrapper">
                {content}
                <div className="pageGen__code">
                    <Highlight className='hljs code-class' style={{"padding": "20px !important", "width": "100px !important" }}>
                        {code}
                    </Highlight></div>
            </div>
        </div>
    );
};

export default ProductsPageGenerator;